<script>
	import { afterUpdate, onMount } from 'svelte';
	import katex from 'katex';
	import html2canvas from 'html2canvas';
  
	let latexInput = "x=\\frac{-b\\pm\\sqrt{b^2-4ac}}{2a}";
	let textArea;
	let outputElement;
	let latexLogo;
	let scale = 5; 
	let backgroundColor = '#ffffff';
	let textColor = '#000000';
	let transparentBackground = false;
	let overlayImageSrc = '';
	let showOverlay = false;

	onMount(() => {
	  renderLatex();
	  renderLogo();
	});

	afterUpdate(() => {
		adjustHeight();
		adjustLatexStyle();
	});

	function renderLogo() {
		katex.render("\\LaTeX", latexLogo, {
			throwOnError: false
		});
	}

	function adjustHeight() {
		if (textArea) {
			// This ensures the textarea has a base size before adjusting
			textArea.style.height = 'auto'; 
			// Set the height to scrollHeight to fit the content
			textArea.style.height = `${textArea.scrollHeight}px`;
		}
	}

	function adjustLatexStyle() {
		if (outputElement) {
			outputElement.style.color = textColor;
		}
	}

	// Call adjustHeight whenever latexInput changes
	$: latexInput, renderLatex();
	$: textColor, backgroundColor, adjustLatexStyle();

	function renderLatex() {
	    if (outputElement) {
			katex.render(latexInput, outputElement, {
				displayMode: true,
				throwOnError: false
			});
	    }
		adjustHeight();
	}

	async function saveImage() {
		const canvas = await html2canvas(outputElement, {
			scale: scale,
			backgroundColor: transparentBackground ? 'transparent' : backgroundColor 
		});
		const a = document.createElement('a');
		a.href = canvas.toDataURL('image/png');
		a.download = 'latex.png';
		a.click();
	}

	async function showImage() {
		const canvas = await html2canvas(outputElement, {
			scale: scale,
			backgroundColor: transparentBackground ? 'transparent' : backgroundColor 
		});
		overlayImageSrc = canvas.toDataURL('image/png');
		showOverlay = true;
	}

	function hideOverlay() {
		showOverlay = false;
	}

</script>
 
<style>
.auto-height-textarea {
	min-height: 10px;
	height: 10px;
	overflow-y: hidden; /* Prevent scroll bar flash */
	resize: none; /* Optional: prevent manual resizing */
}

.textarea.latex-input {
    background-color: darkslategrey;
    color: white;
    font-family: monospace;
}
.latex-output {
	padding-left: 10px;
	padding-right: 10px;
	display: inline-block;
    font-size: 3em;
}
.slider {
	width: 200px;
}

.save button {
	width: 150px;
	margin-bottom: 20px;
}
.color-picker {
	display: flex;
	align-items: top;
	gap: 20px;
}
/* .text-color, .bg-color {
	margin-right: 20px;
} */
/* .color-label {
	margin-bottom: 5px;
} */

.scale-range {
	margin-bottom: 10px;
}
.color-label {
	margin-bottom: 5px;
}

.transparent {
	margin-top: 5px;
}
.checkbox {
	margin-right: 5px;
}
.bg-color {
	display: flex;
	flex-direction: column;
}
.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}

.overlay img {
	max-width: 80%;
	max-height: 80%;
}

</style>

<section class="section">
	<div class="container content">
		<h1><span class="latex-logo" bind:this={latexLogo}></span><span style="margin-left: 10px;">Previewer</span></h1>
		<textarea class="latex-input textarea auto-height-textarea" rows="1" bind:this={textArea} bind:value={latexInput}></textarea>
		<div class="latex-output" bind:this={outputElement}></div>
		<hr>
		<h2>Save</h2>
		<div class="save">
			<button class="save-image button" on:click={saveImage}>Save Image</button>
			<button class="show-image button" on:click={showImage}>Show Image</button>
			<!-- <button class="copy-image button" on:click={copyImage} bind:this={copyImageButton}>Copy Image</button> -->
			<div class="scale-range">
				<div class="scale-text">
					Scale: {scale}
				</div>
				<input type="range" min="0.5" max="10" step="0.01" bind:value={scale} class="slider">
			</div>
			<div class="color-picker">
				<div class="text-color">
					<div class="color-label">Text Color</div>
					<input type="color" bind:value={textColor} />
				</div>
				<div class="bg-color">
					<div class="color-label">Background Color</div>
					<input type="color" bind:value={backgroundColor} />
					<div class="transparent">
						<input class="checkbox" type="checkbox" bind:checked={transparentBackground} />Transparent
					</div>
				</div>
			</div>
		</div>
	</div>
	{#if showOverlay}
        <div class="overlay" on:click={hideOverlay} on:keydown={hideOverlay}>
            <img src={overlayImageSrc} alt="LaTeX Output">
        </div>
    {/if}
</section>